$(function () {
    $('.js-timepicker-range').flatpickr({
        enableTime: true,
        noCalendar: true,
        dateFormat: "H:i",
        defaultDate: "00:00",
        time_24hr: true,
        onChange: function(selectedDates, dateStr, instance) {
            updateLimitation({
                $input: $(instance.element),
                dateStr: dateStr,
                instance: instance,
            });
        },
        onOpen: [
            function(selectedDates, dateStr, instance){
                updateLimitationConfig({
                    instance: instance,
                });
            },
        ],
    });

    function updateLimitation(props) {
        const $container = props.$input.closest('.timepicker-range');
        const $from = $container.find('.js-timepicker-range-from');
        const $to = $container.find('.js-timepicker-range-to');

        if(props.$input.hasClass('js-timepicker-range-from')) {
            updateMinTime({
                instance: props.instance,
                dateStr: props.dateStr,
            });
        } else {
            updateMaxTime({
                instance: props.instance,
                dateStr: props.dateStr,
            });
        }
    }

    function updateMaxTime(props) {
        const $container = $(props.instance.element).closest('.timepicker-range');
        const $from = $container.find('.js-timepicker-range-from');
        let date = new Date(props.instance.selectedDates);
        date.setMinutes( date.getMinutes() - 1 );

        const hours = date.getHours().toString().length < 2 ? '0' + date.getHours() : date.getHours();
        const minutes = date.getMinutes().toString().length < 2 ? '0' + date.getMinutes() : date.getMinutes();

        $from.attr('data-max-time', hours + ':' + minutes);
    }

    function updateMinTime(props) {
        const $container = $(props.instance.element).closest('.timepicker-range');
        const $to = $container.find('.js-timepicker-range-to');
        let date = new Date(props.instance.selectedDates);
        date.setMinutes( date.getMinutes() + 1 );

        const hours = date.getHours().toString().length < 2 ? '0' + date.getHours() : date.getHours();
        const minutes = date.getMinutes().toString().length < 2 ? '0' + date.getMinutes() : date.getMinutes();

        $to.attr('data-min-time', hours + ':' + minutes);
    }

    function updateLimitationConfig(props) {
        const $element = $(props.instance.element);
        const max = $element.attr('data-max-time');
        const min = $element.attr('data-min-time');

        if(typeof max === 'string' && max.length) {
            props.instance.config.maxTime = max;

        }

        if(typeof min === 'string' && min.length) {
            props.instance.config.minTime = min;
        }
    }
});