export default class Dropdown {
    constructor({$opener, $dropdown}) {
        this.$opener = $opener;
        this.$dropdown = $dropdown;
    }

    init() {
        if(this.validateProps()) return false;

        this.$opener.on('click', (e) => {
            e.preventDefault();
            this.toggleDropdown();
        });
    }

    toggleDropdown() {
        if(this.$dropdown.hasClass('opened')) {
            this.hideDropdown();
        } else {
            this.showDropdown();
        }
    }

    hideDropdown() {
        this.$dropdown.hide().removeClass('opened');
        this.$opener.removeClass('opened');
    }

    showDropdown() {
        this.$dropdown.show().addClass('opened');
        this.$opener.addClass('opened');
    }

    validateProps() {
        if(!this.$opener instanceof jQuery) {
            console.error(this.$opener, ' is not jQuery object');
            return false;
        }

        if(!this.$dropdown instanceof jQuery) {
            console.error(this.$dropdown, ' is not jQuery object');
            return false;
        }
    }
}