export default class More {
    constructor() {
        this.$containers = $('.js-more');
        this.$openers = this.$containers.find('.more__opener');
    }

    init() {
        if(!this.$containers.length) return false;

        this.$openers.on('click', (evt) => {
            evt.preventDefault();
            this.toggleInner({
                $container: $(evt.currentTarget).closest('.js-more'),
            });
        })

        $(document).on('click', (evt) => {
            if (!$('.js-more').has(evt.target).length && $('.js-more').has('.opened').length) {
                this.hideInner({
                    $container: $('.more__opener.opened').closest('.js-more'),
                });
            }
        })
    }

    toggleInner(props) {
        const $inner = props.$container.find('.more__inner');

        if($inner.hasClass('opened')) {
            this.hideInner({
                $container: props.$container,
            });
        } else {
            this.showInner({
                $container: props.$container,
            });
        }
    }

    showInner(props) {
        const $inner = props.$container.find('.more__inner');
        const $opener = props.$container.find('.more__opener');

        $inner.addClass('opened');
        $opener.addClass('opened');
        $inner.stop().show();
    }

    hideInner(props) {
        const $inner = props.$container.find('.more__inner');
        const $opener = props.$container.find('.more__opener');

        $inner.removeClass('opened');
        $opener.removeClass('opened');
        $inner.stop().hide();
    }
}