$(document).on('click', '.add-fav-js', function(e) {
    e.preventDefault()
    let index,
        id = $(this).data('id'),
        tplus_fav = $.cookie('tplus_fav')

    if(tplus_fav === undefined) {
        tplus_fav = []
    } else {
        tplus_fav = JSON.parse(tplus_fav)
    }
    index = tplus_fav.indexOf(id)
    if(index < 0) {
        tplus_fav.push(id)
    } else {
        tplus_fav.splice(index, 1)
    }
    $.cookie('tplus_fav', JSON.stringify(tplus_fav), {path: '/'})
})
