export default class Acco {
    constructor() {
        this.$container = $('.js-acco');
        this.$openers = this.$container.find('.acco__opener');
        this.$items = this.$container.find('.acco__item');
    }

    init() {
        this.$items.each((index, item) => {
            const mode = $(item).attr('data-mode');

            if(mode === 'opened') {
                this.initContent({
                    $item: $(item),
                });
            }
        });

        this.$openers.on('click', (evt) => {
            evt.preventDefault();
            this.toggleContent({
                $item: $(evt.currentTarget).closest('.acco__item'),
            });
        });
    }

    initContent(props) {
        const $content = props.$item.find('.acco__content');
        const $title = props.$item.find('.acco__title');

        props.$item.attr('data-mode', 'opened');
        $content.stop().show();
        $title.stop().hide();
    }

    toggleContent(props) {
        const mode = props.$item.attr('data-mode');

        if(mode === 'hidden') {
            this.showContent({
                $item: props.$item,
            });
        } else {
            this.hideContent({
                $item: props.$item,
            });
        }
    }

    showContent(props) {
        const $content = props.$item.find('.acco__content');
        const $title = props.$item.find('.acco__title');

        props.$item.attr('data-mode', 'opened');
        $content.stop().slideDown();
        $title.stop().slideUp();
    }

    hideContent(props) {
        const $content = props.$item.find('.acco__content');
        const $title = props.$item.find('.acco__title');

        props.$item.attr('data-mode', 'hidden');
        $content.stop().slideUp();
        $title.stop().slideDown();
    }
}