$(function () {
    if($('.js-datepicker-range').length) {
        $(".js-datepicker-range").flatpickr({
            locale: global.flatpickr.l10ns.ru,
            dateFormat: "d.m.Y",
            mode: "range",
            onChange: function (selectedDates, dateStr, instance) {
                updateDatepickerRange({
                    $container: $(instance.element).closest('.datepicker-range'),
                    dateStr: dateStr,
                });
            },
        });

        function updateDatepickerRange(props) {
            const dateFrom = getDateFrom({dateStr: props.dateStr});
            const dateTo = getDateTo({dateStr: props.dateStr});
            const $from = props.$container.find('.datepicker-range__from .datepicker-range__text');
            const $to = props.$container.find('.datepicker-range__to .datepicker-range__text');

            $from.html(dateFrom);
            $to.html(dateTo);
        }

        function getDateFrom(props) {
            let result = '';
            const regExp = / —/i;

            if(regExp.test(props.dateStr)) {
                result = props.dateStr.slice(0, props.dateStr.search(regExp));
            } else {
                result = props.dateStr;
            }

            return result;
        }

        function getDateTo(props) {
            let result = '';
            const regExp = /— /i;

            if(regExp.test(props.dateStr)) {
                result = props.dateStr.slice((props.dateStr.search(regExp) + 2), props.dateStr.length);
            }

            return result;
        }
    }
});