$(function () {
    $('.js-rater .rater__box').rate();

    $(".js-rater .rater__box").on("change", function(ev, data){
        const $container = $(ev.currentTarget).closest('.js-rater');
        const $input = $container.find('.rater__input');

        $input.length ? $input.val(data.to) : null;
    });
})