export default class Chatbot {
    constructor() {
        this.$container = $('.js-chat-bot');
        this.$menuOpener = this.$container.find('.chat-bot__menu-opener');
    }

    init() {
        this.$menuOpener.on('click', (evt) => {
            evt.preventDefault();
            this.toggleMenu({
                $container: $(evt.currentTarget).closest('.js-chat-bot'),
            });
        });
    }

    toggleMenu(props) {
        const $menu = props.$container.find('.chat-bot__menu');

        if($menu.hasClass('opened')) {
            this.hideMenu({
                $container: props.$container
            });
        } else {
            this.showMenu({
                $container: props.$container
            });
        }
    }

    hideMenu(props) {
        const $opener = props.$container.find('.chat-bot__menu-opener');
        const $menu = props.$container.find('.chat-bot__menu');

        $opener.removeClass('opened');
        $menu.removeClass('opened');
        $menu.stop().slideUp();
    }

    showMenu(props) {
        const $opener = props.$container.find('.chat-bot__menu-opener');
        const $menu = props.$container.find('.chat-bot__menu');

        $opener.addClass('opened');
        $menu.addClass('opened');
        $menu.stop().slideDown();
    }
}