$(function () {
    $('.js-select').select2({
        minimumResultsForSearch: -1,
        width: 'resolve',

        // insert image
        templateSelection: formatSelectionState,
        templateResult: formatResultState,
    });

    function formatSelectionState(state) {
        return formatState(state, "selection")
    }

    function formatResultState(state) {
        return formatState(state, "result")
    }

    function formatState(state, mode) {
        const imgName = $(state.element).data("image")

        if (imgName) {
            const
                imgDir = "./img/content",
                imgPath = imgDir + "/" + imgName

            return $(`
                <div class="form__select-option form__select-option--${mode}">
                    <div class="form__select-image">
                        <img class="form__select-img" src="${imgPath}">
                    </div>
                    <div class="form__select-text">
                        ${state.text}
                    </div>
                </div>
            `);
        } else {
            return state.text
        }
    };
});
