export default class Burger {
    constructor() {
        this.$opener = $('.burger__opener');
        this.$content = $('.header__burger-content-scroll');
        this.$overlay = $('.overlay');
        this.$body = $('body');
        this.$closer = $('.burger__close');
    }

    init() {
        if(this.validateProps()) return false;

        this.$opener.on('click', (evt) => {
            evt.preventDefault();
            this.toggleBurger();
        });

        this.$overlay.on('click', () => {
            this.toggleBurger();
        })

        this.$closer.on('click', (evt) => {
            evt.preventDefault();
            this.hideBurger();
        });
    }

    toggleBurger() {
        if(this.$content.hasClass('opened')) {
            this.hideBurger();
        } else {
            this.showBurger();
        }
    }

    hideBurger() {
        this.$overlay.removeClass('opened');
        this.$opener.removeClass('opened');
        this.$content.removeClass('opened');
        this.$body.removeClass('scroll-off');
    }

    showBurger() {
        this.$overlay.addClass('opened');
        this.$opener.addClass('opened');
        this.$content.addClass('opened');
        this.$body.addClass('scroll-off');
    }

    validateProps() {
        if(!this.$opener instanceof jQuery) {
            console.error(this.$opener, ' is not a jQuery object');
            return false;
        }

        if(!this.$content instanceof jQuery) {
            console.error(this.$opener, ' is not a jQuery object');
            return false;
        }

        if(!this.$overlay instanceof jQuery) {
            console.error(this.$overlay, ' is not a jQuery object');
            return false;
        }

        if(!this.$opener.length) {
            console.error(this.$opener, ' is not a exist');
            return false;
        }

        if(!this.$content.length) {
            console.error(this.$content, ' is not a exist');
            return false;
        }

        if(!this.$overlay.length) {
            console.error(this.$overlay, ' is not a exist');
            return false;
        }

    }
}