$(function () {
    if($('.js-modal-opener').length) {

        $('.js-modal-opener').magnificPopup({
            type: 'inline',
            preloader: false,
            focus: '#username',
            modal: true
        });
        $(document).on('click', '.js-modal-closer', function (e) {
            e.preventDefault();
            $.magnificPopup.close();
        });
    }
});