$(function () {
   if($('.js-file').length) {

       $('.js-file').each(function (index, input) {
           addFileName($(input));
       });

       $('.js-file').on('change', function () {
           addFileName($(this));
       });
   }

   function addFileName($input) {
       const box = $input.closest('.file');
       const nameBox = box.find('.file__name');
       const files = $input[0].files;
       const placeholder = $input.attr('placeholder');

       if(files.length) {
           nameBox.html(files[0].name);
           nameBox.removeClass('file__name--placeholder');
       } else {
           nameBox.html(placeholder);
           nameBox.addClass('file__name--placeholder');
       }
    }
});